.scroll-container {
    display: flex;
    overflow-x: auto;
    gap: 10px;
  }
  
  .post-card, .repo-card {
    flex: 0 0 auto;
    width: 200px;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    background: #fff;
  }
  
  .post-card img {
    width: 100%;
    height: auto;
  }
  